import { Metadata } from 'next';
import { notFound } from 'next/navigation';
import Link from 'next/link';
import { format } from 'date-fns';
import { Eye, Download } from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';
import Header from '@/components/Header';
import LeftSidebar from '@/components/LeftSidebar';
import RightSidebar from '@/components/RightSidebar';
import Footer from '@/components/Footer';

// Sanity & Data Utils
import { SanityContentRenderer } from '@/lib/sanityContent';
import { urlFor } from '@/lib/sanity';
// You will need to create these server-side fetch functions (example below)
import { getBlogPostBySlug, getAllBlogSlugs } from '@/lib/sanity';

// Client Component for Navigation
// import BackButton from './BackButton'; 

interface Props {
  params: {
    slug: string;
  };
}

// 1. GENERATE STATIC PARAMS (SSG)
// This tells Next.js which pages to build at compile time
export async function generateStaticParams() {
  const slugs = await getAllBlogSlugs();
  return slugs.map((slug: string) => ({
    slug,
  }));
}

// 2. GENERATE METADATA (SEO)
// Replaces React Helmet
export async function generateMetadata({ params }: Props): Promise<Metadata> {
  const post = await getBlogPostBySlug(params.slug);

  if (!post) {
    return {
      title: 'Article Not Found',
    };
  }

  const title = post.seo?.metaTitle || post.title;
  const description = post.seo?.metaDescription || post.excerpt || `Read ${post.title}`;
  const ogImage = post.featuredImage 
    ? urlFor(post.featuredImage).width(1200).height(630).fit('crop').url() 
    : 'https://www.clclaw.in/clc-logo.png';
    
  // Incorporating your specific SEO keywords
  const keywords = [
    'pet services', 'dog training', 'dog grooming', 'pet boarding', // Primary
    'Udaipur', 'pet services in Udaipur', // Geographical
    post.category?.name || 'pet care', 
    post.title
  ].join(', ');

  return {
    title: `${title} | Commercial Law Chamber`,
    description: description,
    keywords: keywords,
    authors: [{ name: "Commercial Law Chamber" }],
    openGraph: {
      title: title,
      description: description,
      type: 'article',
      url: `https://www.clclaw.in/blog/${post.slug.current}`,
      images: [{ url: ogImage, width: 1200, height: 630 }],
      siteName: 'Commercial Law Chamber',
      publishedTime: post.publishedAt,
    },
    twitter: {
      card: 'summary_large_image',
      title: title,
      description: description,
      images: [ogImage],
    },
    alternates: {
      canonical: `https://www.clclaw.in/blog/${post.slug.current}`,
    }
  };
}

// 3. MAIN PAGE COMPONENT (Server Component)
export default async function BlogPage({ params }: Props) {
  const blogPost = await getBlogPostBySlug(params.slug);

  if (!blogPost) {
    notFound(); // Triggers the Next.js 404 page
  }

  const publishedDate = blogPost.publishedAt 
    ? new Date(blogPost.publishedAt).toISOString() 
    : new Date().toISOString();

  // JSON-LD Structured Data
  const jsonLd = {
    "@context": "https://schema.org",
    "@type": "BlogPosting",
    "headline": blogPost.title,
    "description": blogPost.seo?.metaDescription || blogPost.excerpt,
    "image": blogPost.featuredImage ? urlFor(blogPost.featuredImage).url() : null,
    "datePublished": publishedDate,
    "author": {
      "@type": "Organization",
      "name": "Commercial Law Chamber"
    }
  };

  return (
    <div className="min-h-screen">
      {/* Inject Structured Data */}
      <script
        type="application/ld+json"
        dangerouslySetInnerHTML={{ __html: JSON.stringify(jsonLd) }}
      />

      <div className="max-w-7xl bg-slate-900/40 mx-auto px-4 sm:px-6 lg:px-8 py-4">
        <Header />
        
        <div className="flex flex-col lg:flex-row gap-4">
          {/* Left Sidebar */}
          <div className="hidden lg:block lg:w-48 xl:w-52">
            <LeftSidebar activeSection="insights" />
          </div>

          {/* Main Content */}
          <div className="flex-1 min-w-0">
            <article className="bg-white/95 backdrop-blur-sm p-8">
              <div className="max-w-4xl">
                
                {/* Back Navigation (Client Component) */}
                {/* <nav className="mb-6" aria-label="Breadcrumb">
                  <BackButton />
                </nav> */}
                
                <header>
                  <h1 className="text-2xl sm:text-3xl lg:text-4xl font-bold poppins text-[#163C0F] mb-6">
                    {blogPost.title}
                  </h1>
                  
                  {blogPost.category && (
                    <div className="mb-4">
                      <span className="inline-block bg-primary/10 text-primary px-3 py-1 rounded-full text-sm font-medium">
                        {blogPost.category.name}
                      </span>
                    </div>
                  )}
                  
                  {blogPost.publishedAt && (
                    <time className="text-sm text-gray-600" dateTime={publishedDate}>
                      {format(new Date(blogPost.publishedAt), 'MMMM d, yyyy')}
                    </time>
                  )}
                </header>
                
                {/* Featured Image */}
                {blogPost.featuredImage && (
                  <div className="mb-8">
                    <img
                      src={urlFor(blogPost.featuredImage).width(800).height(400).fit('crop').url()}
                      alt={blogPost.featuredImage.alt || blogPost.title}
                      className="w-full h-auto rounded-lg shadow-md"
                      loading="eager" // Important for LCP
                    />
                  </div>
                )}
                
                {/* Content */}
                <div className="text-gray-700">
                  <SanityContentRenderer content={blogPost.content} />
                </div>
                
                {/* Download Button */}
                {blogPost.downloadUrl && (
                  <div className="flex items-center gap-3 mt-8">
                    <Button variant="outline" asChild>
                      <a href={blogPost.downloadUrl} target="_blank" rel="noopener noreferrer">
                        <Eye className="h-4 w-4" />
                        Read The Full Document Here
                      </a>
                    </Button>
                  </div>
                )}
              </div>
            </article>
          </div>

          {/* Right Sidebar */}
          <div className="hidden lg:block lg:w-60 xl:w-64">
            <RightSidebar />
          </div>
        </div>
        
        <Footer />
      </div>
    </div>
  );
}